change_class_refund=function(swimmerId, fromClassId, toClassId){

    ////////////check/////////
    //Á°Æ‰øùswimmerIdÂ∑≤Ê≥®ÂÜåfromClassId Âπ∂‰∏îÊú™ËøõË°å cancelÊàñchangeÊìç‰Ωú
    var registerItem =DB.ClassesRegister.findOne({
        swimmerId:swimmerId,
        classId:fromClassId
    })
    if(!registerItem){
        throw new Meteor.Error(500, swimmerId+' did not register class '+fromClassId);
    }else if(registerItem.status!='normal'){

        throw new Meteor.Error(500, 'class is under status '+registerItem.status);
    }

    //check Êï∞ÁõÆ


    //////////////////////


    //var cart = change_get_or_create_active_cart()
    //var cart_id = cart._id

    var cart_id = change_create_cart()

    console.log('1-----'+cart_id)


    //ÊûÑÈÄ†ÊîπÂèòËØæÁ®ãÊó∂ÁöÑitem
    var item = {
        type: 'change',
        swimmerId: swimmerId,
        fromClassId: fromClassId,
        toClassId: toClassId,

        //extra info todo only pick special fields
        swimmer: DB.Swimmers.findOne({_id: swimmerId}),
        fromClass: DB.Classes.findOne({_id: fromClassId}),
        toClass: DB.Classes.findOne({_id: toClassId})

    }



    ///////////////////////////////////////////////
    //ÂÆåÊàêchange classË¥≠Áâ©ËΩ¶ÂàõÂª∫
    var result = DB.ShoppingCart.update({
        '_id': cart_id
    }, {
        '$set': {status: 'pending'},//Â∑≤ÂºÄÂßã‰∫ãÂä°Â§ÑÈáå ÁΩÆ‰∏∫pending  todo touchÊó∂Èó¥
        '$push': {
            'items': item  //item.quantity==1 or -1
        }
    })


    console.log('2-----'+result)


    ////////////////////change_move_pending_to_applied/////////////////

    //Âç†Áî®‰∏Ä‰∏™class  todo Á°Æ‰øù$push ÂîØ‰∏Ä
    //ÈúÄÂÖàÊü•ËØ¢ todo Á°Æ‰øù‰∏çÂ≠òÂú®‰ª•ÂâçÁöÑchangeÂéÜÂè≤ Âê¶ÂàôÊ∏ÖÁêÜ‰ª•ÂâçÁöÑÊìç‰Ωú


    //ÂêéÈù¢Âü∫‰∫é ‰ª•ÂâçÊú™ÂÅöËøáÁõ∏ÂêåÁöÑchangeÂπ∂‰∏îÂõ†‰∏∫ÁâπÊÆäÂéüÂõ†ËÄå‰∏≠Êñ≠
    result = DB.Classes.update(
        {
            '_id': toClassId,
            'seatsRemain': {'$gte': 1}
        },
        {
            '$inc': {'seatsRemain': -1},
            '$push': {
                'carted': {
                    'cartId': cart_id,

                    'type': 'change',
                    'fromClassId': fromClassId,
                    'toClassId': toClassId,

                    'swimmerId': swimmerId,
                    'quantity': 1,  //changeÊó∂Ë¥≠Áâ©ËΩ¶Êó†Êï∞Èáè classÈáåÈúÄË¶ÅÊï∞Èáè Áî®‰∫éÊÅ¢Â§çÊó∂Âå∫ÂàÜÊòØfromËøòÊòØto
                    'timestamp': new Date()
                }
            }
        })

    console.log('3-----'+result)


    if (!result) {//Âç†Áî®Â§±Ë¥• Êï∞ÁõÆ‰∏çÂ§üÊó∂
        DB.ShoppingCart.update(
            {'_id': cart_id},
            {
                '$set': {status: 'active'}, //ÈáçÁΩÆ‰∏∫active
                '$pull': {
                    'items': {
                        'type': 'change',
                        'fromClassId': fromClassId,
                        'toClassId': toClassId,
                    }
                }
            })
        //todo Áõ¥Êé•Âà†Èô§Ë¥≠Áâ©ËΩ¶Ôºü

        throw new Meteor.Error(500, 'add_class_to_cart error ËØæÁ®ãÂç†Áî®Â§±Ë¥•');

    }

    //else {
    //    DB.ShoppingCart.update(
    //        {'_id': cart_id},
    //        {
    //            '$set': {status: 'active'} //ÈáçÁΩÆ‰∏∫active
    //        })
    //    return {cartId: cart_id}
    //}



    //Ê†áËÆ∞oldclass
    var oldRegister =DB.ClassesRegister.update({
        swimmerId:swimmerId,
        classId:fromClassId
    },{
        '$set': {status: 'changing'},
        '$push': {
            'carted':cart_id
        }
    })


    console.log('4-----'+oldRegister)
    App.info = App.info || DB.App.findOne()

    // ÊèíÂÖ•Âπ∂Ê†áËÆ∞newclass
    var newRegister =DB.ClassesRegister.insert({
        sessionId: App.info.sessionRegister,
        account:Meteor.userId(),
        swimmerId:swimmerId,
        classId:toClassId,
        cartId:cart_id,
        status: 'changing',
        'carted':[cart_id]
    })

    console.log('5-----'+newRegister)


    ////// Âà∞Ëææapplied Áä∂ÊÄÅ
    result=DB.ShoppingCart.update(
        {'_id': cart_id},
        {
            '$set': {status: 'applied'} //ÈáçÁΩÆ‰∏∫active
        })

    console.log('6-----'+result)


    //Â∑≤ÊàêÂäüÈÄÄËØæ Ê∏ÖÈô§‰∫ãÂä°Á∫™ÂΩï
    //change_move__applied_to_donejia
    //todo Â¢ûÂä† changed canceled ÊÄÅ‰øùÊåÅÂéÜÂè≤Êìç‰ΩúÔºü


    console.log('change_move__applied_to_done');

    result =DB.ClassesRegister.remove({
        swimmerId:swimmerId,
        classId:fromClassId,
        status:'changing'
    });
    console.log('7-----'+result)

    result = DB.ClassesRegister.update({
        swimmerId:swimmerId,
        classId:toClassId,
        status:'changing'
    },{
        $set:{status: 'normal'},
        $pull:{'carted':cart_id}
    })
    console.log('8-----'+result)


    //ÊóßËØæÁ®ãÊï∞ÁõÆÔºã1
    result =DB.Classes.update(
        {
            '_id': fromClassId
        },
        {
            '$inc': {'seatsRemain': 1}
        })

    console.log('9-----'+result)


    //Êñ∞ËØæÁ®ã Ê∏ÖÈô§‰∫ãÂä°Á∫™ÂΩï
    result =DB.Classes.update(
        {
            '_id': toClassId
        },
        {
            '$pull': {
                'carted': {
                    'cartId': cart_id,

                    'type': 'change',
                    'fromClassId': fromClassId,
                    'toClassId': toClassId,

                    'swimmerId': swimmerId,
                    'quantity': 1  //changeÊó∂Ë¥≠Áâ©ËΩ¶Êó†Êï∞Èáè classÈáåÈúÄË¶ÅÊï∞Èáè Áî®‰∫éÊÅ¢Â§çÊó∂Âå∫ÂàÜÊòØfromËøòÊòØto
                }
            }
        })
    console.log('10-----'+result)


    result =DB.ShoppingCart.update(
        {'_id': cart_id},
        {
            '$set': {status: 'done'} //ÈáçÁΩÆ‰∏∫active
        })


    console.log('11-----'+result)




}


